<template>
    <div class="bindSite">
        <Head class='Head' :data='Title+"绑定"'></Head>
        <div class='bindSite_cen'>
            <van-cell-group>
                <van-field
                label="昵称"
                disabled
                :placeholder="nickName"/>

                <van-field
                label="绑定地址"
                v-model="site"
                placeholder="请输入绑定地址"/>
            </van-cell-group>
            <p class='sign'>注：一个账户相同地址只可绑定一次，请谨慎绑定！</p>
        </div>
        <van-button class='btn' @click='bind()' size="large">绑定地址</van-button>
    </div>
</template>

<script>

import Head from '../Head';
import axios from 'axios';

export default {
    name: 'bindSite',
    components:{ Head },
    data() {
        return {
            Title:this.$route.query.num,
            nickName:localStorage.nickName,
            site:''
        }
    },
    methods:{
        bind(){
            let url = window.globalData.config.domain + '/blockchain/userFillInAddress';
            axios.post(url,{
                cmd:'123',
                body:{
                    uid:localStorage.uid,
                    appid:'11',
                    token:this.Title,
                    address:this.site
                }
            }).then( (res) => {
                let data = res.data;
                if( data.code === 0 && data.body.exist === 0 ){
                    this.$dialog.alert({
                        message: '地址绑定成功'
                    })
                    this.$router.go(-1);
                }else if( data.code === 0 && data.body.exist === 1 ){
                    this.$dialog.alert({
                        message: '一个账号只能绑定一个地址，您已经绑定过该地址'
                    })
                    this.$router.go(-1);                    
                }else if( data.code === 136 ){
                    this.$dialog.alert({
                        message: '您输入的地址有误，请您重新输入地址'
                    })
                    this.site = '';
                }else{
                    this.$dialog.alert({
                        message: '地址绑定失败'
                    })
                    this.$router.go(-1);                    
                }
            })
        }
    }
}
</script>

<style scoped lang='scss'>
.bindSite{
    font-size: 18px;
    .bindSite_cen{
        padding-top: 64px;
    }
    .btn{
        border: none;
        background: #ff7f3b;
        color: white;
        position: absolute;
        bottom: 0;
    }
    .sign{
        margin-top: 10px;
        font-size: 14px;
        text-align: center;
        color: green;
    }
}
</style>
